import f2Img from "./../assets/img/f2.jpg";
import avatarImg from "./../assets/img/avatar.jpg";

const ADD_MES = "dialogs/ADD-MES";
let initialState = {
    dialogsData: [
        {id: 1, name: ""},
        {id: 2, name: ""},
        {id: 3, name: ""},
        {id: 4, name: ""},
        {id: 5, name: ""}
    ],
    messagesData: [
        {id: 1, avatar: f2Img , name: "", message: "   "},
        {id: 2, avatar: avatarImg, name: "", message: "  "},
        {id: 3, avatar: f2Img , name: "", message: "       "},
        {id: 4, avatar: avatarImg, name: "", message: "    "},
        {
            id: 5,
            avatar: f2Img ,
            name: "",
            message: "  ," +
            "x   ,  ,    "
        },
    ]
};

const dialogsReducer = (state = initialState, action) => {
    switch (action.type) {
        case ADD_MES: {
            let newMes = {
                id: 6,
                avatar: avatarImg,
                name: "",
                message: action.mes
            };
            return {
                ...state,
                messagesData: [...state.messagesData, newMes],
            }
        }
        default:
            return state;
    }
}

export const addMesActionCreator = (mes) => ({type: ADD_MES, mes})


export default dialogsReducer;